
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'

import MtaH5 from 'mta-h5-analysis'
import { getDetail } from '@/utils/api'
import Share from '@/utils/share'

export default {
name: 'story_detail',
components: {
XHeader,
ViewBox
},
data(){
return {
content: '',
title: '正在加载'
}
},
props: {
id: {
type: String,
default: 0
}
},
watch: {
async 'id' (newId) {
if (Number(newId) < 0) {
return
}
this.getStory(newId)
window.scrollTo(0, 0)
}
},
methods: {
async getStory(id) {
MtaH5.pgv()
const loading = this.$vux.loading
loading.show('拼命加载中')
try {
const data = await getDetail(id)
this.title = data.title

var findstr = '<div class="img-place-holder"></div>'
var replacestr = '<div class="img-place-holder">\
<img src=' + data.image +' />\
<h1 class="headline-title">'+ data.title +'</h1>\
<span class="img-source">图片：'+ data.image_source +'</span>\
</div>'
let body = data.body.replace(findstr, replacestr);
body = body.replace(/src=\"http:/g, 'src="')
this.content = body
loading.hide()
} catch (err) {
loading.hide()
}
},
sharePage() {
let share = new Share({
title: this.title,
desc: '来自知乎日报',
link: location.href
})
share.call('qqFriend')
}
},
created () {

MtaH5.init({
sid:'500575360', //必填，统计用的appid
cid: '500645799',
autoReport: 0,//是否开启自动上报(1:init完成则上报一次,0:使用pgv方法才上报)
senseHash: 1, //hash锚点是否进入url统计
senseQuery: 1, //url参数是否进入url统计
performanceMonitor: 1 
})
this.getStory(this.id)
}
}



